.contactsAlert {
    font-style: italic;
    font-weight: bold;
    color: black;
}
*:focus {
    outline: none;
}
.secondButton{
    margin-left: 20px;
}
body {
    background-color: #efefef;
    font-family: "Open Sans", "Arial", snas-serif;
}
.validateAlert{
    padding-top: 5px;
    margin-left: 450px;
    color: red;
    font-weight: bold;
    font-style: italic;
    width: 50px;
    word-break: unset;
}
.content {
    display: grid;
    grid-template-columns: 50% 50% ;
    grid-template-rows: 100%;
}

.leftColumn {
grid-column: 1;
    grid-row: 1;
margin-left: 50px;
    width: 350px;
}
.rightColumn {
grid-column: 2;
    grid-row: 1;

    width: 350px;
}

.inputGroup {
    display: block;
    width: 441px;
    max-width: 100%;
    height: 82px;
    border: 0;
    background-color: #ffffff;
    border-bottom-left-radius: 41px;
    border-bottom-right-radius: 41px;
    border-top-left-radius: 41px;
    border-top-right-radius: 0;
    box-shadow: 0 17px 40px 0 rgba(75, 128, 182, 0.07);
    margin-bottom: 22px;
    position: relative;
    font-size: 17px;
    color: #a7b4c1;
    transition: opacity 0.2s ease-in-out, filter 0.2s ease-in-out,
    box-shadow 0.1s ease-in-out;
}

.inputGroup:hover {
    box-shadow: 0 14px 44px 0 rgba(0, 0, 0, 0.077);
}

.inputGroup input {
    position: absolute;
    border: 0;
    box-shadow: none;
    background-color: rgba(255, 255, 255, 0);
    top: 0;
    height: 65px;
    width: 100%;
    padding: 0 53px;
    box-sizing: border-box;
    z-index: 3;
    display: block;
    color: #1a6fc4;
    font-size: 17px;
    font-family: "Oxygen", sans-serif;
    transition: top 0.1s ease-in-out;
}

.inputGroup input::placeholder {
    color: rgba(0, 0, 0, 0);
}

.inputGroup input:focus,
.inputGroup input:not(:placeholder-shown) {
    top: 17px;
}

.inputGroup label {
    position: absolute;
    border: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    padding: 0 53px;
    box-sizing: border-box;
    transition: all 0.1s ease-in-out;
    cursor: text;
}

.inputGroup input:focus + label,
.inputGroup input:not(:placeholder-shown) + label {
    bottom: 20px;
    font-size: 13px;
    opacity: 0.7;
}

.reqMark {
    position: absolute;
    pointer-events: none;
    top: 0;
    right: 33px;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 22px;
    color: #e0e0e0;
    font-family: "Ubuntu", sans-serif;

}
