
.posts{
    display: grid;
    grid-column: 1/14;
    grid-row: 3/15;

}

.input_posts {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(3, 1fr);
}

.myposts {

    grid-column: 1/15;
    grid-row: 1;
    font-family: "Comic Sans MS";
    font-size: x-large;
    margin: 0px;
    margin-left: 20px;
    margin-bottom: 0px;


}


.input {

    grid-column: 1/14;
    grid-row: 2/3;
    margin-left: 10px;
    width: 780px;
    height: 40px;
    margin-bottom: 20px;
    margin-top: 0px;
;
}

.input_text {

    width: 100%;
    height: 100%;
    background-color: lightgray;
    border: solid lightgray;
    margin-top: 2%;
}

.input1{

    grid-column: 12;
    grid-row:  3;
 margin-bottom: 0px;
    margin-left: 700px;

}

.input_button {
    background-color: springgreen;
    border: solid springgreen;
    color: black;
    padding: 3px;
    text-align: center;
    font-weight: bold;
    font-style: italic;
    margin-top: 35%;

}

.post {

    grid-column: 1/14;
    margin-top: 0%;
    width: 760px;

}

