
.post {
    display: grid;
    grid-template-columnscolumn: 1/14;
    margin-top: 6%;
}
.post_picture{
    grid-column: 1;

}

.post_picture_size{
    width: 50px;
}

.post1 {
    display: inline-block;
    grid-column: 2/14;
    font-family: "Comic Sans MS";
    font-weight: bold;
    font-size: large;
    margin-top: 10px;
    width: 630px;
    line-break: auto;

}

.likes{
grid-column: 12;
}

.like_png_width{

    margin-right: 5px;
    width:30px;
}

.like_style {
   color: red;
    font-weight: bold;
    font-style: italic;
    text-align: center;
    font-size: x-large;


}